body {
  background-color: #62c2c8;
}
.app{
  background-color: white;
  min-height: 80vh;
  width: 400px;
  margin: 20px auto;
  border-radius: 30px;
   font-family: "Arimo", sans-serif;
}
.crossed {

  text-decoration: line-through 2px red;
  background-color: #f0f0f0;
}

ul, li {
  list-style-type: none;
  padding: 10px;
  margin: 0;
   font-family: "Arimo", sans-serif;
}
.container {
  display: flex;
  justify-content: center;
}

.btn-add {

  background: #65aa06;
  margin-top: 5px;
}

.btn-delete {
  background: #d30100;
  margin-bottom: 5px;
}


.btn {
padding: 10px;
border: none;
width: 70%;
color: white;
font-size: 16px;
font-family: "Arimo", sans-serif;
border-radius: 10px;
}

input{
  flex: 1;
  padding: 8px;
  margin: 10px;
  border-radius: 10px;
  font-family: "Arimo", sans-serif;
}
